.word-wrap{
  height: auto;
  width: 60%;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
  font-size: 2em;
}

.flip-container{
  -webkit-perspective: 250px;
          perspective: 250px;
}
.flipper{
  width: 350px;
  height: 425px;
  margin: auto;
  position: relative;
}

.card{
  width: 100%;
  height: 100%;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0px;
  left: 0px;

}

.front{
  z-index: 2;
  background-color: lightblue;
  border-radius: 10px;
}

.back{
  z-index: 1;
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
  background-color: lightpink;
  border-radius: 10px;
}

.flipper:hover .front{
  z-index: 1;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flipper:hover .back{
  z-index: 2;
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
}



